import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule, Routes} from '@angular/router';
import {SharedModule} from '../shared/shared.module';
import {BrowserModule} from '@angular/platform-browser';
import {FormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';

/*import local components*/
import {
  GarmentPartWidgetComponent,
  LocalGarmentCollarWidgetComponent,
  LocalGarmentRightFrontPanelWidgetComponent,
  LocalGarmentFrontPlacketWidgetComponent,
  LocalGarmentLeftFrontPanelWidgetComponent,
  LocalGarmentShoulderWidgetComponent,
  LocalGarmentHemWidgetComponent,
  LocalGarmentLeftCuffWidgetComponent,
  LocalGarmentSleevesWidgetComponent,
  LocalGarmentSleevePlacketWidgetComponent,
  LocalGarmentRightCuffWidgetComponent,
  LocalGarmentPatchWidgetComponent,
  LocalGarmentBackPanelWidgetComponent,
  LocalGarmentBackHemWidgetComponent,
  LocalGarmentBackYokeWidgetComponent,
  LocalGarmentBackCollarWidgetComponent,
  LocalGarmentBackSleevesWidgetComponent,
} from './sewing-svgshirts-widget/garment-part-widget.component';

import {SewingWidgetComponent} from './sewing-widget.component';
import {GarmentPatchWidgetComponent} from './sewing-svgshirts-widget/garment-patch-widget.component';
import {GarmentCollarWidgetComponent} from './sewing-svgshirts-widget/garment-collar-widget.component';
import {GarmentShoulderWidgetComponent} from './sewing-svgshirts-widget/garment-shoulder-widget.component';
import {GarmentFrontPlacketWidgetComponent} from './sewing-svgshirts-widget/garment-frontplacket-widget.component';
import {GarmentLeftFrontPanelWidgetComponent} from './sewing-svgshirts-widget/garment-leftfrontpanel-widget.component';
import {GarmentRightfrontpanelWidgetComponent} from './sewing-svgshirts-widget/garment-rightfrontpanel-widget.component';
import {GarmentHemWidgetComponent} from './sewing-svgshirts-widget/garment-hem-widget.component';
import {GarmentLeftCuffWidgetComponent} from './sewing-svgshirts-widget/garment-leftcuff-widget.component';
import {GarmentSleevesWidgetComponent} from './sewing-svgshirts-widget/garment-sleeves-widget.component';
import {GarmentSleeveplacketWidgetComponent} from './sewing-svgshirts-widget/garment-sleeveplacket-widget.component';
import {GarmentRightcuffWidgetComponent} from './sewing-svgshirts-widget/garment-rightcuff-widget.component';



import {SewingInfoComponent} from './sewing-info-widget/sewing-info.component';


const routes: Routes = [
  {path: 'PCS-MI/sewing', component: SewingWidgetComponent},
];

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,

    RouterModule.forRoot(routes),
  ],
  declarations: [
    SewingInfoComponent,
    GarmentPartWidgetComponent,
    SewingWidgetComponent,
    /*import garment*/
    GarmentPatchWidgetComponent,
    GarmentSleeveplacketWidgetComponent,
    GarmentCollarWidgetComponent,
    GarmentShoulderWidgetComponent,
    GarmentLeftFrontPanelWidgetComponent,
    GarmentRightfrontpanelWidgetComponent,
    GarmentFrontPlacketWidgetComponent,
    GarmentHemWidgetComponent,
    GarmentLeftCuffWidgetComponent,
    GarmentSleevesWidgetComponent,
    GarmentRightcuffWidgetComponent,

    /*import local mock component*/
    LocalGarmentCollarWidgetComponent,
    LocalGarmentSleevePlacketWidgetComponent,
    LocalGarmentShoulderWidgetComponent,
    LocalGarmentFrontPlacketWidgetComponent,
    LocalGarmentLeftFrontPanelWidgetComponent,
    LocalGarmentHemWidgetComponent,
    LocalGarmentRightFrontPanelWidgetComponent,
    LocalGarmentLeftCuffWidgetComponent,
    LocalGarmentSleevesWidgetComponent,
    LocalGarmentRightCuffWidgetComponent,
    LocalGarmentPatchWidgetComponent,
    LocalGarmentBackPanelWidgetComponent,
    LocalGarmentBackHemWidgetComponent,
    LocalGarmentBackYokeWidgetComponent,
    LocalGarmentBackCollarWidgetComponent,
    LocalGarmentBackSleevesWidgetComponent
  ],
  entryComponents: [
    SewingInfoComponent,
    GarmentPatchWidgetComponent,
    GarmentSleeveplacketWidgetComponent,
    GarmentCollarWidgetComponent,
    GarmentShoulderWidgetComponent,
    GarmentLeftFrontPanelWidgetComponent,
    GarmentRightfrontpanelWidgetComponent,
    GarmentFrontPlacketWidgetComponent,
    GarmentHemWidgetComponent,
    GarmentLeftCuffWidgetComponent,
    GarmentSleevesWidgetComponent,
    GarmentRightcuffWidgetComponent,

    /*Local Mock*/
    LocalGarmentPatchWidgetComponent,
    LocalGarmentLeftCuffWidgetComponent,
    LocalGarmentSleevePlacketWidgetComponent,
    LocalGarmentCollarWidgetComponent,
    LocalGarmentShoulderWidgetComponent,
    LocalGarmentFrontPlacketWidgetComponent,
    LocalGarmentHemWidgetComponent,
    LocalGarmentRightFrontPanelWidgetComponent,
    LocalGarmentLeftFrontPanelWidgetComponent,
    LocalGarmentSleevesWidgetComponent,
    LocalGarmentRightCuffWidgetComponent,
    LocalGarmentBackPanelWidgetComponent,
    LocalGarmentBackHemWidgetComponent,
    LocalGarmentBackYokeWidgetComponent,
    LocalGarmentBackCollarWidgetComponent,
    LocalGarmentBackSleevesWidgetComponent
  ],
  providers: [],
  bootstrap: []
})
export class SewingPageModule {
}



