import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { GarmentNeckTapAppComponent } from './garment-neck-tap-app/garment-neck-tap-app.component';
import {RouterModule, Routes} from '@angular/router';
import {SharedModule} from '../shared/shared.module';
import { MiTextInfoWidgetComponent } from './mi-text-info-widget/mi-text-info-widget.component';
import { MiAttachmentWidgetComponent } from './mi-attachment-widget/mi-attachment-widget.component';
import { GarmentNeckTapWiWidgetComponent } from './garment-neck-tap-wi-widget/garment-neck-tap-wi-widget.component';
const routes: Routes = [
  { path: 'PCS-MI/wi', component: GarmentNeckTapAppComponent},
];

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forRoot(routes),
  ],
  declarations: [GarmentNeckTapAppComponent, MiTextInfoWidgetComponent , MiAttachmentWidgetComponent, GarmentNeckTapWiWidgetComponent]
})
export class WiPageModule { }



