import {Component, Input, OnInit} from '@angular/core';
import {BaseWidget} from '../base-widget';
import {MdDialog} from '@angular/material';

@Component({
  selector: 'app-cutting-process-widget',
  templateUrl: './cutting-process-widget.component.html',
  styleUrls: ['./cutting-process-widget.component.sass']
})
export class CuttingProcessWidgetComponent extends BaseWidget implements OnInit {
  @Input() data;
  constructor(public dialog: MdDialog) {
    super(dialog);
  }
  ngOnInit() {
  }
}



