import {Component, OnInit} from '@angular/core';
import {MiTextInfo, MiTextService} from '../mi-text.service';
import {Observable} from 'rxjs/Observable';
import {MdDialog} from '@angular/material';
import {ImageViewComponent} from '../../shared/image-view/image-view.component';

@Component({
  selector: 'app-mi-text-info-widget',
  templateUrl: './mi-text-info-widget.component.html',
  styleUrls: ['./mi-text-info-widget.component.sass'],
  providers: [MiTextService]
})
export class MiTextInfoWidgetComponent implements OnInit {

  textInfoType = {
    'TDC' : 'TDC',
    'QA': 'QA',
    'COL': 'COL',
    'WARNING': 'Warning'
  };

  selTag = {
    'TDC': true,
    'QA': true,
    'COL': false,
    'Warning': true
  };

  public items: MiTextInfo[];

  public tdcItems: MiTextInfo[];

  public qaItems: MiTextInfo[];

  public colItems: MiTextInfo[];

  public warningItems: MiTextInfo[];

  public getSelectItem(tag: string): Promise<MiTextInfo[]> {
    return null;
  }

  constructor(private dialog: MdDialog, private miTextService: MiTextService) {
  }

  ngOnInit() {
    this.miTextService.initData().then(value => {
      this.items = value;
      this.tdcItems = this.items.filter(i => i.tag === this.textInfoType.TDC);
      this.qaItems = this.items.filter(i => i.tag === this.textInfoType.QA);
      this.colItems = this.items.filter(i => i.tag === this.textInfoType.COL);
      this.warningItems = this.items.filter(i => i.tag === this.textInfoType.WARNING);
    });
  }

  private getMiTextInfo(object) {
     // console.log(this.selItems);
  }

  openImageDialog(miTextInfo: MiTextInfo): void {
    if (miTextInfo.path != null) {
      const dialogRef = this.dialog.open(ImageViewComponent, {
        width: '50%',
        height: '50%',
        data: {url: miTextInfo.path}
      });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
      });
    }
  }
}



