import {Component, Input, ContentChild, TemplateRef, OnInit} from '@angular/core';


@Component({
  selector: 'app-sewing',
  templateUrl: './sewing-widget.component.html',
  styleUrls: ['./sewing-info-widget.sass'],

})
export class SewingWidgetComponent implements OnInit {
  public garmentPartsFront: Array<any> = [
    'collar', 'shoulder',
    'frontplacket', 'leftfrontpanel', 'rightfrontpanel',
    'hem', 'leftcuff', 'sleeves', 'rightcuff', 'sleeveplacket', 'patch']; // load garment parts for this order from service

  public garmentPartsBack: Array<any> = [
    'backpanel', 'backhem', 'backyoke', 'backcollar', 'backsleeve']; // load garment parts for this order from service

  ngOnInit(): void {
  }

}



