import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'infoFilter'
})
export class InfoFilterPipe implements PipeTransform {

  transform(value: ProcessInfo[], args?: any): ProcessInfo[] {
    return value.filter(value2 => value2.type === args);
  }

}



