import {Component, Input, OnInit} from '@angular/core';
import {BaseWidget} from '../base-widget';
import {MdDialog} from '@angular/material';
import {Router} from '@angular/router';

@Component({
  selector: 'app-washing-process-widget',
  templateUrl: './washing-process-widget.component.html',
  styleUrls: ['./washing-process-widget.component.sass']
})
export class WashingProcessWidgetComponent extends BaseWidget implements OnInit {
  @Input() data;

  ngOnInit() {
  }
  constructor(public dialog: MdDialog, private router: Router) {
    super(dialog);
  }
  jump() {
    if (this.data.active) {
      this.router.navigateByUrl('PCS-MI/wi');
    }
  }
}



