import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {HomeViewComponent} from './home-view/home-view.component';
import {OrderSearchComponent} from './order-search/order-search.component';
import {RouterModule, Routes} from '@angular/router';
import {SharedModule} from '../shared/shared.module';
import {SewProcessWidgetComponent} from './sew-process-widget/sew-process-widget.component';
import {SvgLinkComponent} from './svg-link/svg-link.component';
import {CuttingProcessWidgetComponent} from './cutting-process-widget/cutting-process-widget.component';
import {PrintProcessWidgetComponent} from './print-process-widget/print-process-widget.component';
import {EmbroideryProcessWidgetComponent} from './embroidery-process-widget/embroidery-process-widget.component';
import {WashingProcessWidgetComponent} from './washing-process-widget/washing-process-widget.component';
import {FinishingProcessWidgetComponent} from './finishing-process-widget/finishing-process-widget.component';
import {InfoFilterPipe} from './info-filter.pipe';
import {InfoWidgetComponent} from './info-widget/info-widget.component';

const routes: Routes = [
  {path: 'PCS-MI/home', component: HomeViewComponent},
  {path: 'PCS-MI', redirectTo: 'PCS-MI/home', pathMatch: 'full'},
];

@NgModule({
  imports: [
    CommonModule,

    SharedModule,

    RouterModule.forRoot(routes),
  ],
  declarations: [
    HomeViewComponent, OrderSearchComponent, SewProcessWidgetComponent,
    SvgLinkComponent, CuttingProcessWidgetComponent, PrintProcessWidgetComponent,
    EmbroideryProcessWidgetComponent, WashingProcessWidgetComponent, FinishingProcessWidgetComponent, InfoFilterPipe, InfoWidgetComponent
  ],
  entryComponents: [InfoWidgetComponent]
})
export class LandingPageModule {
}



