import {Component, Input, OnInit} from '@angular/core';
import {BaseWidget} from '../base-widget';
import {MdDialog} from '@angular/material';

@Component({
  selector: 'app-print-process-widget',
  templateUrl: './print-process-widget.component.html',
  styleUrls: ['./print-process-widget.component.sass']
})
export class PrintProcessWidgetComponent extends BaseWidget implements OnInit {
  @Input() data;
  ngOnInit() {
  }
  constructor(public dialog: MdDialog) {
    super(dialog);
  }
}



