import { Component, OnInit } from '@angular/core';
import {MiTextService, TDCTextInfo, MiTextInfo} from '../mi-text.service';

@Component({
  selector: 'app-garment-neck-tap-wi-widget',
  templateUrl: './garment-neck-tap-wi-widget.component.html',
  styleUrls: ['./garment-neck-tap-wi-widget.component.sass'],
  providers: [MiTextService]
})
export class GarmentNeckTapWiWidgetComponent implements OnInit {

  public selTag = 'TDC';

  public neckTapItems: TDCTextInfo[];

  constructor(private miTextService: MiTextService) {
  }

  ngOnInit() {
    this.miTextService.initWiData().then(value => {
      this.getMiTextInfo(value);
    });
  }

  private getMiTextInfo(value) {
    this.neckTapItems = value.filter(i => i.tag === this.selTag);
    console.log(this.neckTapItems);
  }
}



