import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {BaseWidget} from '../base-widget';
import {InfoFilterPipe} from '../info-filter.pipe';
import {MdDialog} from '@angular/material';
import {Router} from '@angular/router';

@Component({
  selector: 'app-sew-process-widget',
  templateUrl: './sew-process-widget.component.html',
  styleUrls: ['./sew-process-widget.component.sass']
})
export class SewProcessWidgetComponent extends BaseWidget implements OnInit {

  @Input() data;

  constructor(public dialog: MdDialog, private router: Router) {
    super(dialog);
  }

  ngOnInit() {
  }

  jump() {
    if (this.data.active) {
      this.router.navigateByUrl('PCS-MI/sewing');
    }
  }
}



