import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {MdDialog} from '@angular/material';
import {VideoViewComponent} from '../../shared/video-view/video-view.component';
import {MiAttachment, MiAttachmentService} from '../mi-attachment.service';
import {ImageViewComponent} from '../../shared/image-view/image-view.component';

@Component({
  selector: 'app-mi-attachment-widget',
  templateUrl: './mi-attachment-widget.component.html',
  styleUrls: ['./mi-attachment-widget.component.sass'],
  providers: [MiAttachmentService]
})
export class MiAttachmentWidgetComponent implements OnInit {

  public attachmentItems: MiAttachment[];

  constructor(private dialog: MdDialog, private attachmentService: MiAttachmentService) {
  }

  ngOnInit() {
    this.initData();
  }

  initData() {
    this.attachmentService.initData().then(value => {
      this.attachmentItems = value;
    });
  }

  openDialog(attachment: MiAttachment): void {
    if (attachment.tag === 'image') {
      this.openImageDialog(attachment);
    } else {
      this.openVideoDialog(attachment);
    }
  }

  openVideoDialog(attachment: MiAttachment): void {
    const dialogRef = this.dialog.open(VideoViewComponent, {
      width: '360px',
      height: '240px',
      data: { url: attachment.path},
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }


  openImageDialog(attachment: MiAttachment): void {
    const dialogRef = this.dialog.open(ImageViewComponent, {
      width: '50%',
      height: '50%',
      data: { url: attachment.path}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }
}



